/* stylelint-disable */
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

/* stylelint-enable */
@font-face {
  font-family: "pt-mono-regular";
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "roboto-condensed-bold";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "roboto-condensed-regular";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-display: swap; }

/*Colors*/
/*Viewports*/
*,
*::before,
*::after {
  box-sizing: inherit;
  scroll-behavior: smooth; }

.body {
  margin: 0 auto;
  padding: 0;
  color: #666666;
  font-family: "pt-mono-regular", "Arial", sans-serif;
  height: 100%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 5%;
  text-align: center;
  font-family: "roboto-condensed-bold", Arial, sans-serif;
  width: 180px;
  border: 1px solid #00b2ff;
  border-radius: 5px; }

.button:hover {
  opacity: 40%; }

.header {
  background-color: #1b2d37;
  color: #ffffff;
  min-height: 950px; }

@media (max-width: 1023px) {
  .header {
    min-height: 685px; } }

@media (max-width: 767px) {
  .header {
    min-height: 647px; } }

.header__wrapper {
  margin: 0 auto;
  padding: 34px 50px 0;
  max-width: 1415px;
  display: flex;
  justify-content: space-between; }

@media (max-width: 1023px) {
  .header__wrapper {
    padding: 20px 25px 0; } }

@media (max-width: 767px) {
  .header__logo-img {
    width: 38px;
    height: 38px; } }

@media (max-width: 767px) {
  .header__wrapper {
    padding: 17px 16px 0; } }

.header__contacts {
  min-width: 50%;
  display: flex;
  justify-content: flex-end; }

.header__contacts a:hover {
  opacity: 40%; }

.header__phone,
.header__mail {
  text-decoration: none;
  padding: 0;
  margin: 0;
  color: #ffffff;
  font-size: 16px;
  line-height: 18px;
  padding-top: 14px;
  display: block;
  min-width: min-content; }

@media (max-width: 1023px) {
  .header__phone,
  .header__mail {
    padding-top: 8px; } }

.header__phone {
  margin-left: 88px; }

@media (max-width: 1023px) {
  .header__phone {
    margin-left: 34px; } }

@media (max-width: 767px) {
  .header__phone {
    margin-left: 23px; } }

.header__mail span {
  padding-left: 10px; }

@media (max-width: 1023px) {
  .header__contacts span {
    display: none; } }

.header__phone span {
  padding-left: 7px; }

@media (max-width: 1023px) {
  .header__mail svg {
    width: 25px;
    height: 19px; } }

@media (max-width: 1023px) {
  .header__phone svg {
    width: 24px;
    height: 24px; } }

.header__button {
  margin-left: 53px;
  background-color: #1b2d37;
  color: #ffffff;
  border-color: #ffffff;
  padding: 5px 10px;
  box-sizing: border-box;
  margin-bottom: 10px; }

@media (max-width: 1023px) {
  .header__button {
    display: none; } }

.main {
  flex: 1 0 auto;
  color: red; }

.footer {
  margin-top: auto; }

.footer__wrapper,
.footer__information-wrapper {
  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;
  display: flex;
  max-width: 1428px;
  margin: 0 auto;
  box-sizing: border-box; }

.footer__wrapper {
  padding: 65px 30px 52px; }

.footer__information-wrapper {
  padding: 14px 30px; }

.footer__wrapper ul {
  list-style: none;
  padding: 0;
  margin: 0; }

.footer__links {
  width: 30%; }

.header__logo {
  display: block;
  margin-bottom: 54px; }

.footer__social {
  display: flex;
  flex-wrap: wrap;
  width: max-content; }

.footer__social li:not(:last-child) {
  margin-right: 16px; }

.footer h2 {
  padding: 0;
  margin: 0;
  margin-bottom: 29px;
  font-family: "roboto-condensed-regular", Arial, sans-serif;
  line-height: 16px;
  text-transform: uppercase; }

.footer li,
.footer p {
  opacity: 50%; }

.footer__navigation {
  width: 45.6%; }

.footer__navigation ul {
  max-height: 100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column; }

.footer__navigation li {
  margin-right: 8%; }

.footer__contacts p {
  padding: 0;
  margin: 0; }

.footer__contacts a {
  text-decoration: none;
  padding: 0;
  margin: 0;
  color: #1b2d37; }

.footer__button {
  display: none; }

.footer__information {
  border-top: 1px solid rgba(27, 45, 55, 0.2); }

.footer__information-wrapper {
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
  width: 100%; }

.footer__information-wrapper p {
  padding: 0;
  margin: 0; }

.footer-container {
  display: flex;
  justify-content: space-between; }

.footer-container p:not(:last-child) {
  padding-right: 53px; }

.footer__information li {
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase; }

.page {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
